.x-axis,
.y-axis {
    path {
        stroke: none;
    }
}

.dm-title {
    font-family: var(--roboto);
    font-size: 1.5rem;
    margin: 0;
    color: var(--alternateWes);
    background-color: #fff;
    padding: 2rem 7rem;
    position: relative;
    &:before {
        content: '';
        display: inline-block;
        position: absolute;
        left: 6rem;
        vertical-align: middle;
        width: .5rem;
        height: 50%;
        margin-right: .5rem;
        background-color: var(--violetWes);
    }
    .block {
        display: block;
    }
}

.dm-a {
    position: absolute;
    font-size: 6rem;
    color: var(--black);
    font-family: var(--roboto);
}
